
.primeravirus {
  display: flex;
  width: 100%;
  height: relative;
  text-align: center;
  justify-content: center;
  align-content: center;
}

.primeravirus svg {
  width: 100%;
  margin-top: 2%;
}

section { width: 80%;
          margin: 0 auto;
          height: 100vh;
      }

.content {
  display: grid;
  grid-template-columns: 70% auto;

  .left {
    position: relative;
    text-align: center;
    display: grid;
    place-items: center;

    p {
      background: white;
      position: absolute;
      margin-top: -25%;
      right: 17%;
      padding: 1em;
      border-radius: 15px;
      width: 20vw;
      text-align: left;
    }

  }

  .img1 {
    width: 50%;
  }

    .right {
      display: grid;
      place-items: center;

      h2{
        color: green;
        font-size: 1.7rem;  
      }

      p {
        line-height: 1.6em;
      }
    }

}

@keyframes anim1 {
  from {
    transform: translateY(-100px);
    opacity: 0;
  }
  
  to {
    transform: translateY(0);
    opacity: 1;
  }
}